ul,
li {
  list-style: none;
}
.page-container {
  max-width: 1348px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}
.container {
  display: flex;
  margin: 100px 50px 50px 50px;
}
.content {
  width: 153px;
  border: 1px solid black;
  margin: 0 auto;
}
.main-container {
  position: relative;
  width: 500px;
  height: 320px;
  background: lightblue;
}
.thecard {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 0.5s ease;
  background: yellow;
}
.thecard:hover,
.thecard:focus {
  transform: rotateY(180deg);
}
.img-box {
  width: 500px;
  height: 320px;
}
.inform-box {
  display: flex;
  flex-direction: column;
  align-items: end;
  padding: 20px;
}
.thefront {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background: #fcf728;
  color: #333;
}
.theback {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background: lightblue;
  transform: rotateY(180deg);
}
.item {
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  display: inline-block;
  font-size: 25px;
  border: 1px solid black;
  cursor: pointer;
  vertical-align: middle;
}
.box {
  width: 200px;
  height: 200px;
  background-color: aquamarine;
  text-align: center;
}
.title-color {
  color: red;
}
.element {
  color: blue;
}
.math-container {
  display: flex;
  flex-direction: row;
  column-gap: 30px;
}
.header-cars-container {
  display: flex;
  max-width: 1000px;
  flex-direction: row;
  justify-content: space-between;
}
.cars-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 20px;
}
.car-item {
  border: 1px solid navy;
  padding: 5px;
}
.basket-list-item {
  display: flex;
  flex-direction: row;
  width: 700px;
  justify-content: space-around;
}
.user-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 30px;
  margin-left: 40px;
  margin-bottom: 30px;
}
.label {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
.user-form-btn {
  display: inline-block;
  padding: 0 20px;
  align-self: self-start;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 3px;
  background-color: bisque;
}
.user-form-btn:hover,
.user-form-btn:focus {
  background-color: aquamarine;
}
.nature-photos {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.photo {
  width: 500px;
}
.color-box {
  display: flex;
  flex-direction: row;
  gap: 30px;
  padding: 30px;
}
.item-green {
  width: 80px;
  height: 80px;
  background-color: green;
  cursor: pointer;
}
.item-red {
  width: 80px;
  height: 80px;
  background-color: red;
  cursor: pointer;
}
.item-blue {
  width: 80px;
  height: 80px;
  background-color: blue;
  cursor: pointer;
}
.modal {
  background-color: aliceblue;
  border: 2px solid black;
  padding: 20px;
}
.card-model {
  background-color: aliceblue;
  border: 2px solid black;
  padding: 20px;
}
/* advertising */
.advertising-block {
  width: 300px;
  height: 200px;
  padding: 40px;
  border-radius: 5px;
  border: 3px solid navy;
  background-color: aquamarine;
}
.advertising-text {
  text-align: center;
}
.calendar {
  width: 900px;
  height: 500px;
  background-image: url(./images/calender/pexels-njeromin-14530327.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.overlay {
 
  height: 100%;
  width: 100%;
  background-color: rgba(191, 168, 168, 0.8); 
  z-index: 1;
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.date-container {
   display: flex;
  flex-direction: row;

  justify-content: space-around;
}
.date-style {
  color: red;
  font-size: 24px;
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.digital-clock {
  font-size: 30px;
  align-self: center;
}
.clock {
  width: 350px;
  height: 350px;
  background-image: url(https://i.ibb.co/dBGxSK1/hiclipart-com-3.png);
  background-color: rgba(255, 255, 255, 0);
  background-size: cover;
  border-radius: 50%;
  position: relative;
  align-self: center;
  /* left: 525px; */
}
.clock-arrow{
  position: absolute;
  background-color: black;
  transform-origin: bottom;
  transform: translateX(-50%);
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
.clock-center {
  position: absolute;  
  background-color: black;
  top: 168px;
  left: 165px;
  width: 20px;
  height: 20px;
  border-radius: 50%;

}
.clock-seconds__arrow {
  bottom: 50%;
  left: 50%;
  width: 5px;
  height: 40%;
}
.clock-minutes__arrow {
  bottom: 50%;
  left: 50%;
  width: 7px;
  height: 33%;
}
.clock-hours__arrow {
  bottom: 50%;
  left: 50%;
  width: 12px;
  height: 24%;
}
.btn-container  {
  display: flex;
  flex-direction: row;
 justify-content:space-around;
}
.randomizer-btn {
  display: block;
  padding: 10px;
  border: navy;
  background-color: rgb(178, 201, 221);
  color: red;
  border-radius: 5px;
}
.randomizer-container {
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
  flex-direction: row;
  gap: 2px;
  justify-content: center;

}
.randomizer-item {
  width: 60px;
  height: 60px;
  border: 2px solid navy;
  border-radius: 5px;
  display: flex;
  padding-top: 10px;
  justify-content: center;
  font-size: 30px;
}
.randomizer-modal {
  width: 600px;
  height: 500px;
  padding: 200px;
  background-color: beige;
  color: blue;
  font-size: 40px;
  text-align: center;
  border: 3px solid green;
  border-radius: 20px;

}
.movie-list {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  column-gap: 30px;
  row-gap: 20px;
}
.movie-card {
  background-color: aliceblue;
  border: 2px solid navy;
  border-radius: 10px;
  padding: 10px;
}
.movie-img {
  display: inline-block;
  width: 300px;
  background-size: cover;
  border-radius: 10px;
  overflow: hidden;
}
.movie-name {
  width: 270px;
}